export type DocumentType = 'order' | 'receipt' | 'bill' | 'report' | 'test';
export type PrintPriority = 'low' | 'normal' | 'high' | 'urgent';
export type PrintStatus = 'pending' | 'printing' | 'printed' | 'failed' | 'cancelled';

interface PrintJobData {
  order_id?: number;
  order_item_id?: number;
  table_id?: number;
  table_name?: string;
  items?: any[];
  total?: number;
  subtotal?: number;
  discount?: number;
  tax?: number;
  payment_method?: string;
  customer_name?: string;
  notes?: string;
  created_at?: string;
  custom_data?: any;
}

interface AddPrintJobParams {
  printer_id?: number;
  document_type: DocumentType;
  document_data: PrintJobData;
  priority?: PrintPriority;
  copies?: number;
  template_id?: number;
}

// Adicionar job √† fila de impress√£o
export async function addPrintJob(params: AddPrintJobParams) {
  try {
    // Se n√£o especificar impressora, buscar a padr√£o baseada no tipo
    let printerId = params.printer_id;
    
    if (!printerId) {
      // Buscar impressora padr√£o para o tipo de documento
      const printerMap = await getDefaultPrintersMap();
      printerId = printerMap[params.document_type] || undefined;
    }

    if (!printerId) {
      throw new Error(`Nenhuma impressora configurada para ${params.document_type}`);
    }

    const response = await fetch('/api/printer-queue', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        printer_id: printerId,
        document_type: params.document_type,
        document_data: params.document_data,
        priority: params.priority || 'normal',
        copies: params.copies || 1,
        template_id: params.template_id,
        status: 'pending',
        retry_count: 0,
        max_retries: 3
      })
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.error || 'Erro ao adicionar job √† fila');
    }

    const job = await response.json();
    return job;
  } catch (error) {
    console.error('Erro ao adicionar job √† fila:', error);
    throw error;
  }
}

// Buscar mapeamento de impressoras padr√£o
async function getDefaultPrintersMap(): Promise<Record<DocumentType, number | null>> {
  try {
    const response = await fetch('/api/printer-profiles');
    const profiles = await response.json();
    
    // Mapear tipo de documento para impressora
    const map: Record<DocumentType, number | null> = {
      order: null,
      receipt: null,
      bill: null,
      report: null,
      test: null
    };

    // Buscar perfil ativo e mapear impressoras
    const activeProfile = profiles.find((p: any) => p.is_active);
    if (activeProfile && activeProfile.settings) {
      const settings = activeProfile.settings;
      map.order = settings.kitchen_printer_id || null;
      map.receipt = settings.receipt_printer_id || null;
      map.bill = settings.bill_printer_id || null;
      map.report = settings.report_printer_id || null;
    }

    return map;
  } catch (error) {
    console.error('Erro ao buscar impressoras padr√£o:', error);
    return {
      order: null,
      receipt: null,
      bill: null,
      report: null,
      test: null
    };
  }
}

// Imprimir pedido
export async function printOrder(data: {
  order_id: number;
  items: any[];
  table_name: string;
  notes?: string;
  priority?: PrintPriority;
}) {
  return addPrintJob({
    document_type: 'order',
    document_data: {
      order_id: data.order_id,
      items: data.items,
      table_name: data.table_name,
      notes: data.notes,
      created_at: new Date().toISOString()
    },
    priority: data.priority || 'high'
  });
}

